// 1.获取图形验证码
import request from '@/utils/request'
export const getPicCode = function () {
  return request({
    url: '/captcha/image',
    method: 'get'
  })
}
// 2.获取短信验证码
export const getMsgCode = function (captchaCode, captchaKey, mobile) {
  return request({
    url: '/captcha/sendSmsCaptcha',
    method: 'post',
    data: {
      form: {
        captchaCode,
        captchaKey,
        mobile
      }
    }
  })
}
// 3.登录接口
export const codeLogin = function (mobile, smsCode) {
  return request({
    url: '/passport/login',
    method: 'post',
    data: {
      form: {
        isParty: false,
        partyData: {},
        mobile,
        smsCode
      }
    }
  })
}
